<template>
  <div class="home">
    <v-container fluid grid-list-lg>
      <v-layout row wrap>
        <v-flex xs12 class="text-xs-center">
          <canvas ref="logo" id="logo" width="200" height="200"></canvas>
        </v-flex>
        <v-flex v-for="(item, index) in items" :key="index" xs12 sm6 md4 lg3>
          <item-card :coverImage="item.image" :url="item.url" :desc="item.desc" :color="item.color" :cardClass="item.class"></item-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import axios from 'axios'
import ItemCard from '../components/ItemCard.vue'
import logoSpritesheet from '../spritesheets/logo'

export default {
  name: 'home',
  components: {
    ItemCard
  },
  // directives: {
  //   insertMessage: function (canvasElement, binding) { }
  // },
  created() {

  },
  mounted() {
    this.init()
    this.loadData()
  },
  methods: {
    init() {
      this.createLogo()
      this.$nextTick(function () {
        window.addEventListener('resize', (e) => {
          // const width = e.target.outerWidth
          // const height = e.target.outerHeight
        })
      })
    },
    createLogo() {
      const createjs = window.createjs
      const stage = new createjs.Stage(this.$refs.logo)
      this.logoStage = stage
      createjs.Ticker.setFPS(24)
      createjs.Ticker.addEventListener('tick', stage)

      const ss = new createjs.SpriteSheet(logoSpritesheet)
      const logo = new createjs.Sprite(ss)
      logo.x = 0
      logo.y = 0
      logo.scaleX = 0.5
      logo.scaleY = 0.5
      logo.on('click', function () {
        this.gotoAndPlay('run')
      })
      stage.addChild(logo)

      setTimeout(() => {
        logo.gotoAndPlay('run')
      }, 500)
    },

    async loadData() {
      try {
        let res = await axios.get('/static/data.json')
        this.items = res.data.articles
      } catch (error) {

      }
    }
  },
  data() {
    return {
      logoStage: null,
      exampleContent: 'This is TEXT',
      msg: '',
      items: [],
      items2: [
        {
          image: '/static/images/etao-banner.png',
          url: 'http://telegra.ph/%E4%B8%80%E6%B7%98%E6%94%BB%E7%95%A5-05-27',
          class: '',
          color: 'red',
          desc: `
<div class="subheading">
  親！你知道淘寶購物原來有返利嗎？
  <br>每次淘寶都有機會慳慳慳…💕
</div>
<div class="subheading red--text text--lighten-1 mt-2">
  ⭐️儲分到抵現全程「<b>唔需要</b>」支付寶實名
</div>`,

        },
        {
          image: '/static/images/alipay-simple-banner.png',
          url: 'http://telegra.ph/%E6%94%AF%E4%BB%98%E5%AF%B6HK%E7%B0%A1%E6%98%93%E4%BD%BF%E7%94%A8%E7%AF%87-08-13',
          class: '',
          color: 'light-blue lighten-1',
          desc: `
<div class="subheading">
  不定時有派錢活動，現時最抵嘅淘寶俾錢方法（聲稱0%手續費），你又用過未？
</div>
<div class="subheading blue-grey--text text--darken-1 mt-2">
  💳中銀卡免手續費10倍積分 831完結
</div>
`,
        },
        {
          image: '/static/images/bear-tmall-banner.png',
          url: 'http://telegra.ph/%E5%A4%A9%E8%B2%93%E8%B6%85%E5%B7%BF%E4%B8%AD%E4%BC%8F%E8%A8%98%E9%9B%86%E9%81%8B%E3%81%AE%E7%AB%A0-05-27',
          class: '',
          color: 'grey',
          desc: `
<div class="subheading">
  幫襯天貓超巿但用集運嘅話，<span class="red--text">記得一定一定要揀間會聽速遞員電話既集運</span>，否則隨時中伏一波三折收唔到貨！
</div>
<div class="subheading brown--text text--lighten-1 mt-2">
 🐻以下係真人(!?)真事被天貓玩殘嘅故事...
</div>
`,
        },
      ]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.full-height .flex {
  display: flex
}

.full-height .flex>.card {
  flex: 1 1 auto
}
</style>



