<template>
  <v-card height="100%" :class="cardClass">
    <v-card-media :class="color">
      <a :href="url" target="_blank"><img :src="coverImage"></a>
    </v-card-media>
    <v-card-text :class="color" class="ma-0 pa-1"></v-card-text>
    <v-card-title primary-title class="pt-1">
      <div v-html="desc" class="white"></div>
    </v-card-title>
  </v-card>
</template>

<script>
export default {
  name: 'item-card',
  props: {
    coverImage: {
      type: String,
      default: null
    },
    url: {
      type: String,
      default: ''
    },
    desc: {
      type: String,
      default: ''
    },
    color: {
      type: String,
      default: ''
    },
    cardClass: {
      type: String,
      default: ''
    }
  },
  data() {
    return {}
  },
  methods: {
    openUrl() {
      window.open(this.url, '_blank')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



